/*============= FontSize Variable =============*/
$PrimaryFont                    : 'Comfortaa-Regular';
$PrimaryLight                   : 'Comfortaa-Light';
$PrimarySemilight               : 'Comfortaa-Medium';
$PrimaryBold                    : 'Comfortaa-SemiBold';
$PrimarySemibold                : 'Comfortaa-Bold';
$SecondaryFont                  : 'EurostileLtStd-Ex2';


/*============= FontSize Variable =============*/
$FontBase                       : 16px;
$FontSmall                      : ceil(($FontBase * 0.75)); // ~12px
$FontMedium                     : ceil(($FontBase * 0.81)); // ~13px
$FontLarge                      : ceil(($FontBase * 1.50)); // ~24px
$FontXLarge                     : ceil(($FontBase * 1.87)); // ~30px
$FontXXLarge                    : ceil(($FontBase * 2.50)); // ~40px


$ImgPath                        : "../img";
$FontPath                       : "../fonts";


