footer{
    .ftrLinksection{
        background: $PrimaryColor;
        padding: 40px 0;
        
        .InnerWrapper{
            @extend %FlexRow-StartCenter;
            @include RespondTo(IpadDevices) {
                @include FlexWrap(wrap);
                @include JustifyContent(Flex-start);
            }
            >div{
                width: 33.33%;
                @include RespondTo(IpadDevices) {
                    width: 50%;
                    @include FlexWrap(wrap);
                    margin-bottom: 40px;
                    &:last-child{
                        margin-bottom: 0;
                    }
                }
                @include RespondTo(MobileDevices) {
                    width: 100%;
                }
            }
            .logoSection{
                padding-right: 30px;
                @include RespondTo(MobileDevices) {
                    padding-right: 0;
                }
                a{
                    @include Flexbox;
                    @include AlignItems(center);
                    @include Font($PrimaryFont, 30px, $WhiteColor, null, 500);
                    outline: none;
                    text-decoration: none;
                    @include RespondTo(MobileDevices) {
                        font-size: 20px;
                    }
                    svg{
                        @include Size(60px, 60px);
                    }
                    p{
                        margin-left: 10px;
                        margin-bottom: 0;
                    }
                }
                >p{
                    @include Font($PrimaryFont, 16px, $WhiteColor, null, 400);
                    margin-top: 30px;
                    @include RespondTo(MobileDevices) {
                        font-size: 14px;
                    }
                }
            }
            .linkSection{
                h3{
                    @include Font($PrimaryFont, 24px, $WhiteColor, null, 600);
                    @include RespondTo(MobileDevices) {
                        font-size: 20px;
                    }
                }
                ul{
                    li{
                        
                        a{
                            padding: 10px 0;
                            @include Font($PrimaryFont, 16px, $WhiteColor, null, 400);
                            text-decoration: none;
                            display:inline-block;
                            transition:0.2s linear;
                            @include RespondTo(MobileDevices) {
                                font-size: 14px;
                            }
                            &:hover{
                                padding-left: 10px;
                            }
                        }
                    }
                }
            }
            .contactSection{
                h3{
                    @include Font($PrimaryFont, 24px, $WhiteColor, null, 600);
                    @include RespondTo(MobileDevices) {
                        font-size: 20px;
                    }
                }
                ul{
                    li{
                        padding: 10px 0;
                        @include Flexbox;
                        @include AlignItems(center);
                        i{
                            @include Size(40px, 40px);
                            min-width: 40px;
                            background: $WhiteColor;
                            box-shadow: 0 0 16px 2px rgba($BlackColor, 0.2);
                            @include BorderRadius(50%);
                            display: inline-block;
                            padding:9px;
                            text-align: center;
                            @include Relative;
                            margin-right: 15px;
                            svg{
                            max-height: 100%;
                            width: 100%; 
                            }
                           
                        }
                        a{
                            padding: 10px 0;
                            @include Font($PrimaryFont, 16px, $WhiteColor, 20px, 400);
                            text-decoration: none;
                            display: inline-block;
                            transition:0.2s linear;    
                            @include RespondTo(MobileDevices) {
                                font-size: 14px;
                            }                        
                        }
                    }
                }
            }
        }
    }
    .CopyrightSection{
        .InnerWrapper{
            @extend %FlexRow-CenterBetween;
            padding: 20px 0;
            @include RespondTo(IpadDevices) {
                @include FlexWrap(wrap);
                text-align: center;
            }
            p{                
                margin-bottom: 0;
                @include Font($PrimaryFont, 18px, $FontColor, null, 500);
                @include RespondTo(MobileDevices) {
                    font-size: 14px;
                    line-height: 18px;
                }
            }
            ul{
                @include Flexbox;
                @include AlignItems(center);
                @include JustifyContent(Flex-end);
                @include RespondTo(IpadDevices) {
                    @include JustifyContent(center);
                    margin: 15px auto 0;
                }
                li{
                    
                    a{
                        display: block;
                        padding: 0 10px;
                        svg{
                            @include Size(40px, 40px);
                            path{
                                fill: #000000;
                                transition: 0.3s linear;
                            }
                        }
                        &.facebook{
                            &:hover{
                                svg{
                                    path{
                                        fill:#3B5998;
                                    }
                                }
                            }
                        }
                        &.twitter{
                            &:hover{
                                svg{
                                    path{
                                        fill:#55ACEE;
                                    }
                                }
                            }
                        }
                        &.linkdin{
                            &:hover{
                                svg{
                                    path{
                                        fill:#007AB9;
                                    }
                                }
                            }
                        }
                        &.youtube{
                            &:hover{
                                svg{
                                    path{
                                        fill:#C4302B;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}