.headerMain{  
    width: 100%;
    .main-menu{    
        @include Relative; 
        z-index: 99;
        &.animation{
            -webkit-animation-duration: .5s;
            animation-duration: .5s;
            -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
            will-change: transform,opacity;
            &.swingOutX {
                -webkit-transform-origin: top;
                -webkit-animation-name: swingOutX;
                -webkit-backface-visibility: visible!important;
                -moz-animation-name: swingOutX;
                -moz-backface-visibility: visible!important;
                -o-animation-name: swingOutX;
                -o-backface-visibility: visible!important;
                animation-name: swingOutX;
                backface-visibility: visible!important;
            }
            &.swingInX {
                
                -webkit-transform-origin: top;
                -ie-transform-origin: top;
                -ms-transform-origin: top;
                transform-origin: top;
                -webkit-backface-visibility: visible!important;
                -webkit-animation-name: swingInX;
                -moz-backface-visibility: visible!important;
                -moz-animation-name: swingInX;
                -o-backface-visibility: visible!important;
                -o-animation-name: swingInX;
                backface-visibility: visible!important;
                animation-name: swingInX;
            }
        }
        &.headroom--not-top{
            position: fixed;
            top:0; left:0;
            width: 100%; 
        }
        .menu-container{
            @extend %FlexRow-CenterBetween;
            .logo {
                // margin-left: 20px;
                @include MarginAll(6px, 0, 6px, 0);
                @include RespondTo(MediumDesktop) {
                    margin-left: 0;
                } 
                a{   
                    @include Font(null, 25px, $WhiteColor, null, 600);
                    @include Flexbox;
                    @include AlignItems(center);
                    text-decoration: none;
                    @include RespondTo(MobileDevices) {
                        font-size: 16px;
                    }
                    svg {
                        @include Size(60px, 60px);
                        @include RespondTo(MobileDevices) {
                            width: auto;
                            height: 40px;
                        }
                    }
                    p{
                        margin-bottom: 0;
                        margin-left: 10px;
                        span{
                            color: #4ca1f4;
                        }
                    }
                }
            }
            .navbar-toggler{
                outline: none;
                display: none;
                padding-right: 0;
                @include RespondTo(IpadProDevices) {
                    display: block;
                }
                .navbar-toggler-icon{
                    position: relative;
                    display: inline-block;
                    pointer-events: none;
                    transition: 0.1s;
                    @include Size(33px, 33px);
                    svg{
                        transform: scale(.1);
                        transform-origin: 0 0;
                        position: absolute;
                        top: -33px;
                        left: -33px;
                        &:not(:root) {
                            overflow: hidden;
                        }
                        path {
                            stroke: #ffffff;
                            stroke-width: 30px;
                            stroke-linecap: round;
                            fill: transparent;
                        }
                    }
                }
                .menu-open{
                    display: none;
                }
                &.collapsed{
                    .menu-close{
                        display: none;
                    }
                    .menu-open{
                        display: block;
                    }
                }
            }
            .NavbarMain {
                display: flex;
                flex-direction: row;
                margin-left: 20px; 
                @include RespondTo(IpadProDevices) {
                    flex-direction: row-reverse;
                }
                .navbar-collapse{
                    display: block;
                    @include RespondTo(IpadProDevices) {
                        display: none;
                        @include Absolute($Left: 0, $Right: 0, $Top: 100%);
                        &.show{
                            display: block;
                        }
                    }    
                    ul {
                        display: flex;
                        flex-direction: row;  
                        
                        @include RespondTo(IpadProDevices) {
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            justify-content: center;
                            @include FlexDirection(column);                        
                            width: 100%;
                            z-index: 10;
                            background: $PrimaryColor;
                            padding:0px;
                            padding-bottom: 15px;
                            margin-left: 0px;
                            box-shadow: 0 0 16px rgba($BlackColor, 0.1);
                        }
            
                        li {
                            margin: 0 3px;
                            @include RespondTo(MobileDevices) {
                                margin: 0;
                                width: 100%; 
                            }        
                            a {
                                display: block;
                                padding: 26px 20px;
                                @include Font($PrimaryFont, 20px, $WhiteColor, null, null);
                                text-decoration: none;
                                @include Position(relative); 
                                @include RespondTo(MediumDesktop) {
                                    font-size: 18px;
                                    padding-left: 10px;
                                    padding-right: 10px;
                                    text-decoration: none;
                                }
                                @include RespondTo(IpadDevices) {
                                    // color: $WhiteColor;
                                    padding: 15px 15px;
                                }
                                &:before, &:after {
                                    content: '';
                                    display: block;
                                    border-top: 2px solid;
                                    position: absolute;
                                    bottom: 0px;
                                    height: 2px;
                                    opacity: 0;
                                    transition: all .3s linear;
                                    border-top-color: $SecondaryColor;
                                }
                                &:before {
                                    width: 80%;                          
                                    right: 0;
                                }
                                &:after {
                                    width: 15%; 
                                    left: 0;
                                    margin-right: 8px;
                                }
                                &:hover {
                                    text-decoration: none;
                                    color: $SecondaryColor;
                                    &:after{
                                        opacity: 1;
                                        width: 15%;
                                        -webkit-animation: primary-short .5s linear;
                                        animation: primary-short .5s linear;
                                    }
                                    &:before{
                                        opacity: 1;
                                        width: 80%;
                                        -webkit-animation: primary-long .5s linear;
                                        animation: primary-long .5s linear;
                                    }
                                }
                                &.active {
                                    // border-color: $PrimaryColor;
                                    color: $SecondaryColor;
                                    &:after{
                                        opacity: 1;
                                    }
                                    &:before{
                                        opacity: 1;
                                    }
                                    @include RespondTo(MobileDevices) {
                                        border-color: $WhiteColor;
                                        color: $SecondaryColor;
                                    }
                                }
                            }
                        }
                    }
                }
                .signIn{  
                    display: flex;
                    align-items: center;
                    a{     
                        @include Font($PrimaryFont, 20px, $WhiteColor, null, null);
                        white-space: nowrap;             
                        padding: 8px 25px;
                        @include BorderRadius(20px);
                        border:1px solid $WhiteColor;
                        outline: inherit !important;
                        text-decoration: none;
                        svg{
                            display: none;
                            path{
                                fill: #ffffff !important;
                            }
                        }
                        @include RespondTo(MobileDevices) {
                            padding: 0px;
                            @include BorderRadius(50%);
                            border: 0;
                            svg{
                                @include Size(30px, 30px);
                                display: block;
                            }
                            span{
                                display: none;
                            }
                        }
                        &:hover{
                            background: $SecondaryColor;
                            color: $WhiteColor;
                        }
                    }
                }
            }
        }
    }  
    
}

@keyframes swingInX{
    0% {
        -webkit-transform: perspective(400px) rotateX(-90deg);
        transform: perspective(400px) rotateX(-90deg);
    }
    100% {
        -webkit-transform: perspective(400px) rotateX(0deg);
        transform: perspective(400px) rotateX(0deg);
    }
}
@keyframes swingOutX{
    0% {
        -webkit-transform: perspective(400px) rotateX(0deg);
        transform: perspective(400px) rotateX(0deg);
    }
    100% {
        -webkit-transform: perspective(400px) rotateX(-90deg);
        transform: perspective(400px) rotateX(-90deg);
    }
}

// @keyframes move-short{100% { width:100%; margin-right: 0;}}
// @keyframes move{ 100% {width: 0%;}}

@keyframes primary-short{0%{width:0}50%{width:40%}100%{width:15%}}
@keyframes primary-long{0%{width:100%}50%{width:55%}100%{width:80%}}

.active {
    .header {
        .logo {
            svg {
                path {
                    fill: $WhiteColor;
                }
            }
        }

        .HeaderSocials {
            border-left: 1px solid rgba($WhiteColor, 0.2);

            a {
                border: 1px solid rgba($WhiteColor, 0.5);

                svg {
                    path {
                        fill: $WhiteColor;
                    }
                }

                &:hover {
                    background: $WhiteColor;

                    svg {
                        path {
                            fill: $PrimaryColor;
                        }
                    }
                }
            }
        }

        #MobileNavTrigger {
            .hamburger-box {
                .hamburger-inner {
                    background: $WhiteColor;

                    &::before,
                    &::after {
                        background: $WhiteColor;
                    }
                }
            }
        }

        nav.navbar {
            ul {
                li {
                    a {
                        color: $WhiteColor;

                        &.active {
                            border-color: $WhiteColor;
                            color: $WhiteColor;
                        }
                    }
                }
            }
        }
    }
}
