#LoaderMainDiv {
    @extend %FlexCol-CenterCenter;
    @include Size(100%, 100vh);
    @include Fixed($Left: 0, $Top: 0);

    svg {
        @include Size(200px, 200px);
        margin-top: -50px;
    }
}

#LoaderSvgMain * {
    -webkit-animation-duration: 7s;
    animation-duration: 7s;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    -webkit-animation-timing-function: cubic-bezier(0, 0, 1, 1);
    animation-timing-function: cubic-bezier(0, 0, 1, 1);
}

@-webkit-keyframes LoaderCircleAnimation {
    1.43% {
        -webkit-transform: translate(153.80001068115234px, 166.45999908447266px) rotate(-90deg)
            translate(-153.80001068115234px, -166.45999908447266px);
        transform: translate(153.80001068115234px, 166.45999908447266px) rotate(-90deg)
            translate(-153.80001068115234px, -166.45999908447266px);
    }

    24.29% {
        -webkit-transform: translate(153.80001068115234px, 166.45999908447266px) rotate(-90deg)
            translate(-153.80001068115234px, -166.45999908447266px);
        transform: translate(153.80001068115234px, 166.45999908447266px) rotate(-90deg)
            translate(-153.80001068115234px, -166.45999908447266px);
    }

    100% {
        -webkit-transform: translate(153.80001068115234px, 166.45999908447266px) rotate(-270deg)
            translate(-153.80001068115234px, -166.45999908447266px);
        transform: translate(153.80001068115234px, 166.45999908447266px) rotate(-270deg)
            translate(-153.80001068115234px, -166.45999908447266px);
    }

    0% {
        -webkit-transform: translate(153.80001068115234px, 166.45999908447266px) rotate(-90deg)
            translate(-153.80001068115234px, -166.45999908447266px);
        transform: translate(153.80001068115234px, 166.45999908447266px) rotate(-90deg)
            translate(-153.80001068115234px, -166.45999908447266px);
    }
}

@-webkit-keyframes LoaderCircleAnimation2 {
    1.43% {
        stroke-dasharray: 437.25;
    }

    24.29% {
        stroke-dasharray: 437.25;
    }

    35.71% {
        stroke-dasharray: 20;
    }

    0% {
        stroke-dasharray: 437.25;
    }

    100% {
        stroke-dasharray: 20;
    }
}

@-webkit-keyframes LoaderCircleAnimation3 {
    1.43% {
        stroke-dashoffset: 437.25;
    }

    15.71% {
        stroke-dashoffset: 0;
    }

    0% {
        stroke-dashoffset: 437.25;
    }

    100% {
        stroke-dashoffset: 0;
    }
}

#LoaderCirclePathAnimation {
    fill: none;
    stroke: $PrimaryColor;
    stroke-miterlimit: 10;
    stroke-width: 3px;
    -webkit-animation-fill-mode: backwards, backwards;
    animation-fill-mode: backwards, backwards;
    stroke-dashoffset: 437.25;
    -webkit-animation-name: LoaderCircleAnimation3, LoaderCircleAnimation2;
    animation-name: LoaderCircleAnimation3, LoaderCircleAnimation2;
    -webkit-animation-timing-function: cubic-bezier(0, 0, 1, 1), cubic-bezier(0, 0, 1, 1);
    animation-timing-function: cubic-bezier(0, 0, 1, 1), cubic-bezier(0, 0, 1, 1);
    stroke-dasharray: 437.25;
}

#LoaderSvgGroup {
    -webkit-animation-fill-mode: backwards;
    animation-fill-mode: backwards;
    -webkit-transform: translate(153.80001068115234px, 166.45999908447266px) rotate(-90deg)
        translate(-153.80001068115234px, -166.45999908447266px);
    transform: translate(153.80001068115234px, 166.45999908447266px) rotate(-90deg)
        translate(-153.80001068115234px, -166.45999908447266px);
    -webkit-animation-name: LoaderCircleAnimation;
    animation-name: LoaderCircleAnimation;
    -webkit-animation-timing-function: cubic-bezier(0, 0, 1, 1);
    animation-timing-function: cubic-bezier(0, 0, 1, 1);
}

@-webkit-keyframes LoaderWLogoAnimation {
    0% {
        opacity: 0;
    }

    66.67% {
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}

@keyframes LoaderWLogoAnimation {
    0% {
        opacity: 0;
    }

    66.67% {
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}

@-webkit-keyframes LoaderWLogo2Animation {
    0% {
        opacity: 0;
    }

    66.67% {
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}

@keyframes LoaderWLogo2Animation {
    0% {
        opacity: 0;
    }

    66.67% {
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}

#LoaderWLogoGroup path {
    fill: $PrimaryColor;
}

#LoaderWLogo1 {
    fill: #141414;
    -webkit-animation-fill-mode: backwards;
    animation-fill-mode: backwards;
    opacity: 0;
    -webkit-animation-name: LoaderWLogoAnimation;
    animation-name: LoaderWLogoAnimation;
    -webkit-animation-timing-function: cubic-bezier(0, 0, 1, 1);
    animation-timing-function: cubic-bezier(0, 0, 1, 1);
}

#LoaderWLogo2 {
    fill: #141414;
    -webkit-animation-fill-mode: backwards;
    animation-fill-mode: backwards;
    opacity: 0;
    -webkit-animation-name: LoaderWLogo2Animation;
    animation-name: LoaderWLogo2Animation;
    -webkit-animation-timing-function: cubic-bezier(0, 0, 1, 1);
    animation-timing-function: cubic-bezier(0, 0, 1, 1);
}
