body {
    background: $BodyBackground;
    @extend %FlexCol;
    min-height: 100%;

    @include RespondTo(MediumDesktop) {
        overflow-x: hidden;
    }
}

::-moz-selection {
    background: $PrimaryColor;
    color: $WhiteColor;
}
::selection {
    background: $PrimaryColor;
    color: $WhiteColor;
}

.wrapper {
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 15px;
    @include RespondTo(MobileDevices) {
        padding: 0 15px; 
    }
}

#LayoutMain {
    @include Size(100%, auto);
    flex: 1 0 auto;
}

.ContainerMain {
    width: 100%;
}

.BottomSticked {
    flex-shrink: 0;
}

#LayoutMain {
    @include Size(100%, 100%);

    @include RespondTo(MediumDesktop) {
        overflow-x: hidden;
    }

    @include RespondTo(IpadDevices) {
        height: auto;
    }
}

.section {
    @include Size(100%, 100vh);

    @include RespondTo(SmallDesktop) {
        height: auto;
    }
}
