section {
    &.BannerMain {
        width: 100%;
        // height: 100%;
        background: url(../img/bg-pattern.png);
        position: relative;
        padding-bottom: 200px;
        // overflow-x: hidden;
        @include RespondTo(IpadDevices) {
            padding-bottom: 100px;
        }
        @include RespondTo(MobileDevices) {
            min-height: 450px;
            padding-bottom: 60px;
        }
        
        &:after{
            content: '';
            @include Absolute($Top:0, $Right:-50%, $Left:-50%);
            margin: 0 auto;
            width: 150%;
            height: 100%;
            @include gradient(-90deg, $list3);
            @include BorderRadius(0 0 50% 50%);
            border-bottom: 30px solid #d0d4ec;
            z-index: 1;
            @include RespondTo(MobileDevices) {
                width: 200%;
                border-bottom-width: 20px;
            }
        }
        .wrapper {
            .bannerWrapper {
                @include Size(100%, 100%);
                @extend %FlexRow-CenterCenter;
                max-width: 1400px;  
                position: relative;
                z-index: 10; 
                @include RespondTo(IpadDevices) {
                    padding: 40px 0;
                    @include FlexWrap(wrap);
                }
                @include RespondTo(MobileDevices) {
                    min-height: 500px;
                }
                .images {
                    @include Font(null, 24px, $FontColor, null, null);
                    @include Size(50%, 100%);                                       
                    z-index: 1;
                    padding: 40px 0 40px 40px;
                    @include RespondTo(IpadDevices) {
                        width: 100%;
                        padding: 40px;
                        text-align: center;
                    }
                    img{
                        
                        height: 100%;
                        max-height: 650px; 
                        @include RespondTo(IpadDevices) {
                            max-width: 350px;
                            width: 100%;
                        }
                    }
                    
                }
                .TagLineText{
                    position: relative;                    
                    margin-top: 0px;   
                    width: 50%;
                    z-index: 10; 
                    .type-text{
                        @include Font($PrimaryLight, 40px, $WhiteColor, 1.2, null);
                        margin-bottom: 0;
                        span{
                            font-family: $PrimaryBold;
                            font-weight: 900;
                            color: $SecondaryColor;
                        }
                        @include RespondTo(IpadDevices) {
                            font-size: 32px;  
                        }
                        @include RespondTo(MobileDevices) {
                            font-size: 22px;
                        }
                    }
                    @include RespondTo(IpadDevices) {        
                        margin-top: 15px;
                        width: 100%;
                        text-align: center;
                    }
                    @include RespondTo(MobileDevices) {
                        font-size: 22px;
                    }
                }
            }
        }
        .circleBox{
            @include Flexbox;
            @include AlignItems(center);
            @include JustifyContent(center);
            @include Absolute($Top:-20%, $Right:-12%);
            @include Size(800px, 800px);
            @include BorderRadius(100%);
            background-color: rgba(208, 212, 236,0.05);
            z-index: 2;
            &:after{
                content: '';
                @include Size(700px, 700px);
                @include BorderRadius(100%);
                background-color: rgba(208, 212, 236,0.051);
                @include Absolute($Top:50px, $Left:50px);
                box-shadow: 0 0 16px rgba($BlackColor, 0.1);
            }
            @include RespondTo(IpadDevices) {
                @include Size(550px, 550px);
                right: -25%;
                &:after{
                    @include Size(450px, 450px);
                }
            }
            @include RespondTo(MobileDevices) {
                @include Size(400px, 400px);
                top: -25%;
                right: -50%;
                &:after{
                    @include Size(350px, 350px);
                    top: 25px;
                    left: 25px;
                }
            }
        }          
    } 
    
    &.aboutSection{
        padding: 40px 0;
        background: url(../img/bg-pattern.png);

        .InnerWrapper{
            @extend %FlexRow-CenterBetween;
            @include RespondTo(IpadDevices) {
                @include FlexWrap(wrap);
            }
            .leftside{
                width: 50%;
                padding-right: 15px;
                @include RespondTo(IpadDevices) {
                    width: 100%;
                    padding-right: 0;
                }
                p{
                    text-align: justify;
                    @include Font($PrimaryFont, 16px, $FontColor, 30px, null);
                    +p{
                        margin-top: 30px;
                    }
                }
            }
            .rightside{
                width: 50%;
                padding-left: 30px;
                @include RespondTo(IpadDevices) {
                    padding-top: 30px;
                    width: 100%;
                    padding-left: 0;
                }
            }
        }
    }

    &.WhatMakesDifferent{
        padding: 40px 0;
        background: $WhiteColor;
        .InnerWrapper{
            @extend %FlexRow-CenterCenter;
            @include RespondTo(IpadDevices) {
                @include FlexWrap(wrap-reverse);
            }
            .rightside{
                width: 50%;
                padding-left: 15px;
                @include RespondTo(IpadDevices) {
                    width: 100%;
                    padding-left: 0;
                }
                p{
                    text-align: justify;
                    @include Font($PrimaryFont, 16px, $FontColor, 30px, null);
                    +p{
                        margin-top: 30px;
                    }
                }
            }
            
            .leftside{
                width: 50%;
                @include RespondTo(IpadDevices) {
                    padding-top: 30px;
                    width: 100%;
                    padding-left: 0;
                }
                .differentImage{
                    padding: 0;
                    .firstRow, .lastRow{
                        @extend %FlexRow-CenterCenter;
                    }
                    .Item{
                        width: 250px;
                        text-align: center;
                        padding: 15px 20px;
                        margin: 0 20px;
                        @include Relative;
                        z-index: 10;
                        @include RespondTo(MobileDevices) {
                            padding: 15px 10px;
                            margin: 0 10px;
                            width: 150px;
                        }
                        i{
                            @include Size(100px, 100px);
                            display: block;
                            margin: 0 auto 15px;
                            background: $WhiteColor;
                            img{
                                width: 100%;
                            }
                            @include RespondTo(MobileDevices) {
                                @include Size(60px, 60px);
                            }
                        }
                        p{
                            @include Font($PrimaryFont, 16px, $FontColor, 30px, 600);
                            margin-bottom: 0;
                            @include RespondTo(MobileDevices) {
                                @include Font($PrimaryFont, 14px, $FontColor, 20px, 600);
                            }
                        }  
                        .dashedBorder{
                            width:75px;
                            height: 200px;
                            display: inline-block;
                            border-width: 2px;
                            border-style: dashed;
                            z-index: -1;
                            @include RespondTo(MobileDevices) {
                                width: 50px;
                            }
                            &:before{
                                content: '';
                                @include Size(13px, 13px);
                                @include BorderRadius(50%);
                                background: $PrimaryColor;
                            }
                        }                      
                    }
                    .firstRow{
                        .leftIc{
                            .dashedBorder{                                
                                border-color: $PrimaryColor  $PrimaryColor  transparent transparent;
                                @include BorderRadius(0 20px 0 0);
                                @include Absolute($Right:-4px, $Top:45%);    
                                &:before{
                                    @include Absolute($Left:-5px, $Top:-7px); 
                                }                            
                            }
                        }
                        .rightIc{
                            .dashedBorder{
                                border-color: $PrimaryColor transparent transparent  $PrimaryColor;
                                @include BorderRadius(20px 0 0 0);
                                @include Absolute($Left:-4px, $Top:45%); 
                                &:before{
                                    @include Absolute($Right:-5px, $Top:-7px); 
                                }   
                            }
                        }
                    }
                    .lastRow{
                        .leftIc{
                            .dashedBorder{                                
                                border-color:transparent $PrimaryColor  $PrimaryColor transparent;
                                @include BorderRadius(0 0 20px 0);
                                @include Absolute($Right:-4px, $Bottom:55%);   
                                &:before{
                                    @include Absolute($Left:-5px, $Bottom:-7px); 
                                }                             
                            }
                        }
                        .rightIc{
                            .dashedBorder{
                                border-color: transparent transparent $PrimaryColor  $PrimaryColor;
                                @include BorderRadius(0 0 0 20px);
                                @include Absolute($Left:-4px, $Bottom:55%);
                                &:before{
                                    @include Absolute($Right:-5px, $Bottom:-7px); 
                                }   
                            }
                        }
                    }
                    .centerCircle{
                        @include Size(225px, 225px);
                        @include BorderRadius(50%);
                        @include gradient(-140deg, $list);
                        box-shadow: 0 0 16px rgba($BlackColor, 0.2);
                        margin: 30px auto;
                        @include Flexbox;
                        @include AlignItems(center);
                        @include JustifyContent(center);
                        @include Relative;
                        z-index: 15;
                        @include RespondTo(MobileDevices) {
                            @include Size(180px, 180px);
                        }
                        .innerCircle{
                            @include Size(75%, 75%);
                            @include BorderRadius(50%);
                            @include gradient(0deg, $list1);
                            box-shadow: 0 0 16px rgba($BlackColor, 0.2);
                            @include Flexbox;
                            @include AlignItems(center);
                            @include JustifyContent(center);
                            p{
                                @include Font($PrimaryFont, 16px, $PrimaryColor, null, 500);
                                margin-bottom: 0;
                                text-align: center;
                                @include RespondTo(MobileDevices) {
                                    font-size: 14px;
                                }
                                strong{
                                    display: block;
                                    @include Font($PrimaryBold, 28px, $SecondaryColor, null, null);
                                    @include RespondTo(MobileDevices) {
                                        font-size: 18px;
                                    }
                                }
                                
                            }                              
                        }
                    }
                }
            }
        }
    }

    &.ourServices{
        padding: 40px 0;
        background: url(../img/bg-pattern.png);
        .header{
            text-align: center;
            h2{
                span{
                    right: 0;
                }
            }
        }
        .InnerWrapper{
            .serviceList{
                margin-left: -20px;
                margin-right: -20px;
                @extend %FlexRow-CenterCenter; 
                @include FlexWrap(wrap);
                @include RespondTo(IpadDevices) {
                    margin-left: -15px;
                    margin-right: -15px;
                }
                .list{
                    width: 50%;
                    padding:20px;
                    @include RespondTo(IpadDevices) {
                        width: 100%;
                        padding:15px 15px 0px 15px;
                    }
                    .innerList{
                        background: $WhiteColor;
                        box-shadow: 0 0 16px rgba($BlackColor, 0.2);
                        @extend %FlexRow-CenterCenter; 
                        padding: 20px 0;
                        .LeftIcon{
                            padding: 0 10px 0 15px;
                            width: 100px;
                            @include RespondTo(MobileDevices) {
                                padding: 0 10px 0 10px;
                                width: 80px;
                            }
                        }
                        .rightContent{
                            width: calc(100% - 100px);
                            padding: 0 15px 0 10px;
                            @include RespondTo(MobileDevices) {
                                width: calc(100% - 80px);
                            }
                            h3{
                                @include Font($PrimaryBold, 26px, $SecondaryColor, null, null);
                                margin-bottom: 20px;
                                @include RespondTo(MobileDevices) {
                                    font-size: 18px;
                                    margin-bottom: 10px;
                                }
                            }
                            p{
                                @include RespondTo(MobileDevices) {
                                    font-size: 14px;
                                    line-height: 18px;
                                } 
                            }
                        }
                    }
                }
            }
        }
    }
    &.APIFeatures{
        padding: 40px 0;
        background:$WhiteColor;
        .header{
            text-align: center;
            h2{
                @include Font($PrimaryBold, 40px, $PrimaryColor, null, null);
                @include RespondTo(MobileDevices) {
                    font-size: 30px;
                }
                p{
                    display: inline;
                    margin-left: 6px;
                    color: $SecondaryColor;
                }
                span{
                    right: 0;
                }
            }
        }
        .InnerWrapper{
            .apiKeyList{
                margin-left: -20px;
                margin-right: -20px;
                @extend %FlexRow-CenterCenter; 
                @include FlexWrap(wrap);
                @include RespondTo(IpadDevices) {
                    margin-left: -15px;
                    margin-right: -15px;
                }
                .list{
                    padding:20px;
                    width: 33.33%;
                    @include RespondTo(IpadDevices) {
                        width: 50%;
                        padding:15px;
                    }
                    @include RespondTo(MobileDevices) {
                        width: 100%;
                    }
                    .listInner{
                        text-align: center;
                        i{
                            @include Size(100px, 100px);
                            display: inline-block;
                            text-align: center;
                            margin: 0 auto;
                            @include Relative;
                            svg{
                                width: 100%;
                            }
                            @include RespondTo(IpadDevices) {
                                @include Size(80px, 80px);
                            }
                            @include RespondTo(MobileDevices) {
                                @include Size(60px, 60px);
                            }
                        }
                        .title{
                            display: block;
                            padding: 15px 0;
                            text-align: center;
                            h3{
                                @include Font($PrimaryBold, 18px, $DefaultSvgColor, null, null);
                                margin: 0;
                            }
                        }
                    }
                }
            }
        }
    }
    
    &.StartBussinessSection{
        padding: 60px 0 60px;
        background: $PrimaryColor url(../img/support-bg.png);
        background-repeat: no-repeat;
        background-position: center top;
        background-size: contain;
        .InnerWrapper{
            text-align: center;
            h3{
                @include Font($PrimaryBold, 40px, $WhiteColor, 40px, null);
                margin-bottom: 20px;
                @include RespondTo(MobileDevices) {
                    font-size: 26px;
                    line-height: 30px;
                } 
            }
            p{
                @extend %FlexRow-CenterCenter; 
                @include Font($PrimaryLight, 18px, #dbe2f9, null, null);
                text-align: center;
                @include RespondTo(MobileDevices) {
                    font-size: 14px;
                    line-height: 18px;
                } 
            }
            .process-item{
                @extend %FlexRow-CenterCenter;
                margin: 50px 0 ;
                @include RespondTo(IpadDevices) {
                    @include FlexWrap(wrap);
                }
                .item{
                    @extend %FlexCol-CenterCenter;
                    width: 25%;
                    @include RespondTo(IpadDevices) {
                        width: 50%;
                        @include FlexWrap(wrap);
                    }
                    @include RespondTo(MobileDevices) {
                        width: 100%;
                        margin-bottom: 30px;
                        &:last-child{
                            margin-bottom: 0;
                        };
                    }
                    i{
                        @include Size(100px, 100px);
                        background: $WhiteColor;
                        box-shadow: 0 0 16px 2px rgba($BlackColor, 0.2);
                        @include BorderRadius(50%);
                        display: inline-block;
                        padding: 20px;
                        text-align: center;
                        @include Relative;
                        svg{
                           max-height: 100%;
                           width: 100%; 
                        }
                        @include RespondTo(MobileDevices) {
                            @include Size(70px, 70px);
                            padding: 12px;
                        }
                    }
                    h3{
                        @include Font($PrimaryFont, 18px, $WhiteColor, null, 600);
                        margin: 15px 0;
                        @include RespondTo(MobileDevices) {
                            font-size: 14px;
                            line-height: 18px;
                        }
                    }
                }
            }
        }
    }
    &.ContactusSection{
        padding: 40px 0;
        background: $WhiteColor;
        .header{
            text-align: center;
            h2{
                @include Font($PrimaryBold, 40px, $PrimaryColor, null, null);
                @include RespondTo(MobileDevices) {
                    font-size: 30px;
                }
                p{
                    display: inline;
                    margin-left: 6px;
                    color: $SecondaryColor;
                }
                span{
                    right: 0;
                }
            }
        }
        .InnerWrapper{
            .row{
                @extend %FlexRow-CenterCenter;
                margin-left: -15px;
                margin-right: -15px;
            }
            .form-group{
                padding-left: 15px;
                padding-right: 15px;
                margin-bottom: 30px;
                width: 100%;                    
            }
            .form-control{
                width: 100%;
                padding: 15px 0;
                border: 0;
                border-bottom: 2px solid #cacaca;
                @include Font($PrimaryFont, 18px, $BlackColor, null, 500);
                @include Relative;
                @include BorderRadius(0);
                outline: none;
                box-shadow: none;
                &:focus{
                    border-color: $SecondaryColor;
                }
            }
            .textarea-control{
                width: 100%;
                padding: 15px 0;
                border: 0;
                border-bottom: 2px solid #cacaca;
                @include Font($PrimaryLight, 18px, $FontColor, null, 500);
                @include Relative;
                resize: none;
            }
            .Primary{
                border: 0;
                box-shadow: none;
                outline: none;
            }
        }
    }
}
