html,
body {
    @include Size(100%, auto);
}

body {
    margin: 0px;
    padding: 0px;
    min-height: 100%;
    @extend %FlexCol;
    @include Font($PrimaryFont, $FontBase, $FontColor, normal, 400);
    letter-spacing: 0.5px;
    background: $WhiteColor;
    overflow-x: hidden;
}

* {
    list-style: none;
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

img {
    max-width: 100%;
    border: none;
}

*:hover,
*:focus,
*:active {
    outline: none;
}

a {
    color: $PrimaryColor;
    text-decoration: none;

    &:hover {
        color: $PrimaryColor;
        text-decoration: underline;
    }
}

ul {
    margin: 0;
}

input,
select,
textarea,
button {
    font-family: $PrimaryFont;
}
