.header{     
    text-align: left;
    margin-bottom: 40px;
    h2{
        @include Font($PrimaryFont, 22px, $PrimaryColor, null, null);
        padding: 20px 0;
        margin-bottom: 0px; 
        @include Relative;
        @include RespondTo(MobileDevices) {
            font-size: 18px;
        }
        strong{
            display: block;
            @include Font($PrimaryBold, 40px, $SecondaryColor, null, null);
            @include RespondTo(MobileDevices) {
                font-size: 30px;
            }
        }
        span{
            display: inline-block;
            width: 100px;
            height: 3px;
            background: #d0d6e2; 
            @include Absolute($Bottom:0, $Left:0);
            @include BorderRadius(6px);
            margin: auto;
            &:before{
                content: '';
                @include Size(20px, 11px);
                @include BorderRadius(10px);
                background: $PrimaryColor;
                @include Absolute($Top:-4px, $Right:0, $Left:0);
                margin: auto;
                border: 2px solid $WhiteColor;
            }
        } 
    }
}
.button-center{
    text-align: center;
}
.border-button{
    padding: 15px 30px;
    background: transparent;
    margin: 0 auto;
    @include Font($PrimaryFont, 18px, $SecondaryColor, null, 500);
    text-align: center;
    display: inline-block;
    border: 2px solid $SecondaryColor;   
    @include BorderRadius(30px); 
    text-transform: uppercase;  
}
  